.botom-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.67;
  letter-spacing: 0.02em;
  color: var(--text-title-color);
  width: 350px;
  margin-bottom: 20px;
  @include desktop {
    width: 450px;
  }

  &.paragraf {
    width: 350px;
    font-size: 14px;
    color: var(--text-title-color);
    margin-bottom: 40px;

    @include desktop {
      width: 450px;
    }
  }
}
